<template>
  <div id="app">
    <div class="nav border-1px">
      <ul ref="navList">
        <li :class="{ active: isActive }" v-for="(item, index) of navItems" :key="index" @click="navActive($event)">
          <router-link :to="navSrc[index]">
            <span :class="'img-' + (index + 1)"></span>
            <span class="text">{{ item }}</span>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="main">
      <router-view :houses="houses"/>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import data from "@/util/mock";

export default {
    name: "App",
    data() {
        return {
            navItems: ["首页", "找室友", "消息", "我的"],
            navSrc: ['/', '/findroommate', '/message', '/user'],
            isActive: false,
            houses: []
        };
    },
    created() {
        axios.get("/house").then(response => {
            this.houses = response.data.houses;
        });
    },
    mounted() {
        this.$refs.navList.children[0].className += ' nav-active'
    },
    methods: {
        navActive(event) {
            event.currentTarget.className += ' nav-active'

            let allSiblings = event.currentTarget.parentNode.children
            for(let i of allSiblings) {
                if(i !== event.currentTarget) {
                    i.className -= ' nav-active'
                }
            }
        }
    }
};
</script>

<style lang="scss">
.nav {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 55px;
    background: #fff;
    border-top: 1px solid #ddd;
    ul {
        display: flex;
        height: 55px;
        li {
            width: 100%;
            height: 100%;
            text-align: center;
            a {
                display: block;
                width: 100%;
                height: 100%;
            }
        }
        @for $item from 1 through 4 {
            .img-#{ $item } {
                display: block;
                margin: 6px auto;
                width: 22px;
                height: 22px;
                background: url("../static/image/nav_" + $item + ".png")
                    no-repeat;
                background-size: 22px 22px;
            }
        }
        .nav-active {
            .text {
                color: #51cbff;
            }
            @for $item from 1 through 4 {
                .img-#{ $item } {
                    background: url("../static/image/nav_" + $item + "_selected" +
                            ".png")
                        no-repeat;
                    background-size: 22px 22px;
                }
            }
        }
    }
}
</style>


