<template>
    <div class="roommate-wrap" ref="roommateWrap">
        <div class="roommate">
            <div class="header" ref="header">
                <p class="title">找室友</p>
                <div class="roommate-nav">
                    <ul>
                        <li>
                            <span>
                                <img src="../../static/image/roommate_filter_nor.png" width="20" height="20">
                            </span>
                            筛选
                        </li>
                        <li>
                            <span>
                                <img src="../../static/image/roommate_publish_icon.png" width="20" height="20">
                            </span>
                            发帖
                        </li>
                        <li>
                            <span>
                                <img src="../../static/image/roommate_reply_icon.png" width="20" height="20">
                            </span>
                            回复
                        </li>
                    </ul>
                </div>
            </div>
            <div class="posts" v-if="posts.length">
                <ul>
                    <li class="border-1px" v-for="(post, index) of posts" :key="index">
                        <div class="post-title clearfix">
                            <span class="has-house" v-if="post.hasHouse">有房</span>
                            <span class="not-house" v-else>无房</span>
                            <p>{{ post.title }}</p>
                        </div>
                        <div class="post-img">
                            <img :src="post.imageSrc" alt="房间图">
                            <img :src="post.imageSrc" alt="房间图">
                            <img :src="post.imageSrc" alt="房间图">
                        </div>
                        <div class="post-info">
                            <img src="../../static/image/detail_landlord_avatar.jpg" alt="头像" width="18" height="18">
                            <span class="post-name">{{ post.roommateInfo.name }}</span>
                            <span v-if="post.roommateInfo.sex === '男'"><img src="../../static/image/icon_male.png" alt="性别男" width="14" height="14"></span>
                            <span v-else><img src="../../static/image/icon_female.png" alt="性别女" width="14" height="14"></span>
                            <span class="post-update-time">1小时前更新</span>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <transition name="nav-fade">
            <div class="roommate-nav-scroll" ref="navScroll">
                <ul>
                    <li>
                        <span>
                            <img src="../../static/image/roommate_filter_nor.png" width="20" height="20">
                        </span>
                        筛选
                    </li>
                    <li>
                        <span>
                            <img src="../../static/image/roommate_publish_icon.png" width="20" height="20">
                        </span>
                        发帖
                    </li>
                    <li>
                        <span>
                            <img src="../../static/image/roommate_reply_icon.png" width="20" height="20">
                        </span>
                        回复
                    </li>
                </ul>
            </div>
        </transition>
    </div>
</template>

<script>
import axios from "axios";
import Mock from "@/util/mock";
import BScroll from "better-scroll";

export default {
    name: "findRoommater",
    data() {
        return {
            posts: []
        };
    },
    created() {
        axios.get("/house").then(response => {
            this.posts = response.data.posts;
        });
    },
    mounted() {
        this._initScroll()
    },
    methods: {
        _initScroll() {
            if (!this.roommateScroll) {
                this.roommateScroll = new BScroll(this.$refs.roommateWrap, {
                    click: true
                });
                this.roommateScroll.on('touchEnd', (pos) => {
                    if(pos.y < -50) {
                        this.$refs.header.style.display = 'none'
                        this.$refs.header.parentNode.style.paddingTop = '85px'
                        this.$refs.navScroll.style.opacity = 1
                    }
                    if(pos.y > -50) {
                        this.$refs.header.style.display = ''
                        this.$refs.header.parentNode.style.paddingTop = '163.6px'
                        this.$refs.navScroll.style.opacity = 0
                    }
                })
            }
        }
    }
};
</script>

<style lang="scss">
@import "../assets/scss/mixin.scss";
.roommate-wrap{
    position: absolute;
    top: 0;
    bottom: 55px;
    left: 0;
    width: 100%;
    overflow: hidden;
    .roommate {
        padding-top: 163.6px;
        .header {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            text-align: center;
            color: #fff;
            background: url("../../static/image/roommate_top_bg.png") center center;
            background-size: cover;
            .title {
                padding: 15px 0 60px 0;
                width: 100%;
                font-size: 14px;
            }
            .roommate-nav {
                ul {
                    display: flex;
                    justify-content: space-around;
                    li {
                        margin-bottom: 10px;
                        span {
                            display: block;
                            margin-bottom: 8px;
                            width: 45px;
                            height: 45px;
                            background: #fff;
                            border-radius: 100%;
                            img {
                                display: block;
                                padding-left: calc(50% - 10px);
                                padding-top: calc(50% - 10px);
                            }
                        }
                    }
                }
            }
        }
        .posts {
            ul {
                > li {
                    margin: 15px 15px;
                    @include border-1px(#e5e5e5);
                    .post-title {
                        margin-bottom: 10px;
                        font-size: 13px;
                        span {
                            display: block;
                            float: left;
                            margin-right: 10px;
                            padding: 3px 7px;
                            color: #fff;
                            font-size: 12px;
                            border-radius: 4px;
                            &.has-house {
                                background: #4fcbff;
                            }
                            &.not-house {
                                background: #8998a0;
                            }
                        }
                        p {
                            line-height: 18px;
                        }
                    }
                    .post-img {
                        width: 100%;
                        display: flex;
                        img {
                            display: block;
                            width: calc(100%/3 - 4px);
                            height: 70px;
                            &:not(:last-of-type) {
                                margin-right: 6px;
                            }
                        }
                    }
                    .post-info {
                        display: flex;
                        align-items: center;
                        padding: 12px 0;
                        img {
                            border-radius: 100%;
                        }
                        .post-name {
                            padding: 0 8px;
                        }
                        .post-update-time {
                            margin-left: auto;
                            color: #95a2aa;
                        }
                    }
                }
            }
        }
    }
    .roommate-nav-scroll {
        position: fixed;
        top: 0;
        left: 0;
        padding-top: 10px;
        width: 100%;
        text-align: center;
        color: #fff;
        background: #55acee;
        opacity: 0;
        &.nav-fade-enter, &.nav-fade-leave-to {
            opacity: 0;
        }
        &.nav-fade-enter-active, &.nav-fade-leave-active {
            transition: opacity 2s;
        }
        ul {
            display: flex;
            justify-content: space-around;
            li {
                margin-bottom: 10px;
                span {
                    display: block;
                    margin-bottom: 8px;
                    width: 45px;
                    height: 45px;
                    background: #fff;
                    border-radius: 100%;
                    img {
                        display: block;
                        padding-left: calc(50% - 10px);
                        padding-top: calc(50% - 10px);
                    }
                }
            }
        }
    }
}
</style>


